/* ------------------------------
   Messages Panel
------------------------------ */
const MessagesPanel = ({ messages, markAsSeen }: any) => {
 
  const handleNewMessage = (id: string | number) => {

    navigate(`message/`); // ‚úÖ appends message id to current route
  };

  const handleOpenMessage : any = (id: string | number) => {
  
    navigate(`message/`); // ‚úÖ appends message id to current route
  };

  return (
    <div className="messages-panel">
      <div className="messages-header">
        <h3>Messages</h3>
        <button 
          className="primary-btn" 
          style={{ fontSize: "12px" }} 
          onClick={handleOpenMessage}
        >
          New Message
        </button>
      </div>

      {messages.length === 0 ? (
        <div className="messages-empty">
          No messages yet ‚Äî this user hasn‚Äôt received or sent any messages.
        </div>
      ) : (
        <div style={{ display: "flex", flexDirection: "column", gap: "8px" }}>
          {messages.map((msg: any) => (
            <div
              key={msg.id}
              onClick={() => {
                markAsSeen(msg.id);
                handleOpenMessage(msg.id); // ‚úÖ open message view
              }}
              style={{
                cursor: "pointer",
                background: msg.unseen ? "var(--accent-bg)" : "transparent",
                fontWeight: msg.unseen ? 600 : 400,
                padding: "10px 12px",
                borderRadius: "8px",
                display: "grid",
                gridTemplateColumns: "1fr auto 50px",
                alignItems: "center",
                transition: "background 0.2s",
              }}
            >
              <div>{msg.text.length > 60 ? msg.text.slice(0, 60) + "..." : msg.text}</div>
              <div style={{ color: "var(--muted)", fontSize: "13px" }}>{formatDate(msg.datetime)}</div>
              <div
                style={{
                  textAlign: "right",
                  color: msg.attachments.length ? "var(--accent)" : "var(--muted)",
                }}
              >
                {msg.attachments.length > 0 ? msg.attachments.length : "‚Äî"}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

